import { FC, forwardRef } from 'react';

import { ForwardProps } from '../../types';

import List, { ListProps } from './List';

const UnorderedList: FC<ListProps & ForwardProps> = forwardRef(function UnorderedList(
  props: ListProps,
  ref: ListProps['ref']
) {
  return <List {...props} variant='unordered' ref={ref} />;
});

export default UnorderedList;
